:root {
  --nyanza: #daf7dc;
  --prussian-blue: #092a49;
  --blue-sapphire: #0b4f6c;
  --platinum: #eaeaea;
  --card-shadow: 0 4px 8px 0 #eaeaea, 0 6px 20px 0 #eaeaea;
  --card-shadow-alt: 0 4px 8px 0 #092a49, 0 6px 20px 0 #092a49;
  --sunglow: #ffd147;
  --rich-black: #070b0d;
  --projects-background: #2e3141;
  --card-background: #353849;
  --font: "Poppins", sans-serif;
}
body {
  margin: 0;
  height: 100vh;
  box-sizing: border-box;
  overflow-x: hidden;
  color: var(--nyanza);
  font-family: var(--font);
  background-color: #2e3141;
}
a {
  text-decoration: none;
  color: var(--nyanza);
  transition: 0.5s ease;
}
a:hover {
  color: var(--sunglow);
  cursor: pointer;
  font-size: 1.35em;
  font-weight: bolder;
  text-decoration: none;
}
/* Navbar */

.navbar-custom {
  height: 8vh;
  color: var(--nyanza);
  display: flex;
  justify-content: space-evenly;
}
.nav-item-custom {
  margin: auto;
}

/* Home Page  */
.home {
  height: 150vh;
  width: 100vw;
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #2e3141;
  background-image: linear-gradient(
      to top,
      rgba(46, 49, 65, 0.9),
      rgba(46, 49, 65, 0.9)
    ),
    url("./assets/mainHomeBg.jpg");
  background-size: auto, cover;
  background-position: center;
  margin-bottom: -6.4em;
  text-transform: uppercase;
}
.home-info-container {
  text-align: justify;
  height: 50%;
  text-justify: inter-word;
  width: 90%;
  margin: auto;
  padding: 10px;
}

.social-container {
  width: 50%;
  margin: auto;
  display: flex;
  justify-content: center;
}
.social-container a {
  padding: 8px;
  transition: 0.5s ease;
}
i.fab:hover {
  color: var(--sunglow);
}

.spacer {
  height: 15vh;
  width: 100vw;
  /* background-color: black; */
}
/* Projects styles */
.project-section {
  height: 100%;
  width: 100vw;
  background-color: #2e3141;
  background-image: linear-gradient(
    to top,
    rgba(46, 49, 65, 0.8),
    rgba(46, 49, 65, 0.8)
  );
  /* color: var(--nyanza); */
}
.projects-header {
  text-transform: uppercase;
  height: 8vh;
  width: 90vw;
  border-bottom: solid 2px rgba(255, 255, 255, 0.125);
  font-size: 2em;
  margin: 1em;
}
.projects-container {
  width: 100vw;
  margin: 2em;
  display: flex;
  justify-content: space-evenly;
  align-content: center;
}
.project-section:before {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(-1);
  box-shadow: inset 0 -1px 0 0 #2e3141, 0 1px 0 0 #2e3141;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='100' height='100' viewBox='0 0 100 100' preserveAspectRatio='none'%3E%3Cpolygon points='0,100 100,0 100,100' style='fill:%232e3141%3B' /%3E%3C/svg%3E");
}

.wrapper {
  background-color: #2e3141;
  margin: 6.5em 0 2em 0;
  position: relative;
}

.wrapper:before {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  content: "";
  display: block;
  height: 6.5em;
  position: absolute;
  width: 100%;
}

.wrapper:before {
  left: 0;
  top: -6.5em;
}

.card {
  background-color: var(--card-background);
  color: var(--nyanza);
  margin: 1em;
  width: 25em;
}

.card-img-top {
  width: 25em;
  height: 14em;
}
.card-img-top.BookCase {
  background-image: url("./assets/bookcase.JPG");
  background-position: center;
  background-size: cover;
}
.card-img-top.NewScript {
  background-image: url("./assets/newscript.JPG");
  background-position: center;
  background-size: cover;
}
.projects {
  display: flex;
  justify-content: center;
  /* flex-direction: column; */
}

.project-links {
  display: flex;
  justify-content: center;
}

.project-link {
  margin: 1em;
}

.live-app {
  text-transform: uppercase;
  font-family: var(--font);
  background-color: var(--nyanza);
  color: var(--card-background);
  font-size: 1.05em;
}
.live-app:hover {
  background-color: var(--sunglow);
  color: var(--rich-black);
}
@media only screen and (max-width: 426px) {
  .projects {
    display: flex;
    /* justify-content: center; */
    flex-direction: column;
  }
}
